const axios = require('axios').default;
import { Notify } from 'notiflix/build/notiflix-notify-aio';
import { createMarkup } from './js/createMarkup';//  
import SimpleLightbox from "simplelightbox";
import "simplelightbox/dist/simple-lightbox.min.css";
import LoadMoreBtn from './js/loadMoreBtn';//   loadmorebtn
import { getRefs } from './js/getRefs';//  
import NewAPI from './js/API'//  

const newApiService = new NewAPI();//       
const refs = getRefs();//  

refs.searchField.addEventListener('submit', handleSubmit);

function handleSubmit(e) {
    e.preventDefault();
    newApiService.query = e.currentTarget.searchQuery.value.trim();//   query   set    

    if (newApiService.query === '') {
        clearMarkup();
        loadMoreBtn.hide();
        return Notify.failure('Request cannot be empty');
        
    }

    loadMoreBtn.show();// 
    clearMarkup();//
    newApiService.resetPage(); // c 
    runSimpleLightBox();
    fetchAll();

}

const loadMoreBtn = new LoadMoreBtn({
    selector: '.load-more',
    hidden: true,
});//     loadmore  
console.log(loadMoreBtn);

loadMoreBtn.refs.button.addEventListener('click', fetchAll);//     


function fetchAll() {
    loadMoreBtn.disable();
    newApiService.fetchArticles()
        .then(card => {
            loadMoreBtn.enable();
            runMarkup(card); // 
           lightbox.refresh(); //  lightbox
        });
}

function runMarkup(c) {//  
    refs.getGallery.insertAdjacentHTML('beforeend', createMarkup(c));
}

function clearMarkup() {//  
    refs.getGallery.innerHTML = '';
}

function runSimpleLightBox() {
    lightbox = new SimpleLightbox('.gallery .gallery__link', {
        captionsData: 'alt',
        captionDelay: 250,
    });

}






